<template>
  <v-container
    fluid
    grid-list-md>
    <v-layout
      row
      wrap>
      <v-flex md12>
        <v-form
          novalidate
          enctype="multipart/form-data"
          @submit.prevent="validateBeforeSubmit"
        >
          <v-card class="elevation-0">
            <v-card-title>
              <h3 class="headline mb-0">Identificação</h3>
            </v-card-title>
            <v-card-text>
              <v-layout
                row
                wrap>
                <v-flex md12>
                  <v-autocomplete
                    v-validate="'required'"
                    v-model="form.redeId"
                    :items="redes"
                    :disabled="!!form.id"
                    :error-messages="errors.collect('Rede')"
                    data-vv-name="Rede"
                    label="Rede"
                    item-value="id"
                    item-text="descricao"
                    required
                  />
                </v-flex>
                <v-flex md9>
                  <v-text-field
                    v-validate="'required'"
                    v-model="form.nome"
                    :error-messages="errors.collect('Nome Empresa')"
                    data-vv-name="Nome Empresa"
                    label="Nome Empresa"
                    required
                  />
                </v-flex>
                <v-flex md3>
                  <v-autocomplete
                    v-validate="'required'"
                    v-model="form.tpRepasseId"
                    :error-messages="errors.collect('Tipo de Repasse')"
                    :items="repasses"
                    label="Tipo de Repasse"
                    item-value="id"
                    item-text="descricao"
                    data-vv-name="Tipo de Repasse"
                    required
                  />
                </v-flex>
                <v-flex md5>
                  <v-text-field
                    v-model="form.nomeProprietario"
                    label="Nome Proprietário"
                  />
                </v-flex>
                <v-flex md4>
                  <v-text-field
                    v-validate="'email'"
                    v-model="form.email"
                    :error-messages="errors.collect('E-mail')"
                    label="Email"
                    type="email"
                    data-vv-name="E-mail"
                  />
                </v-flex>
                <v-flex md3>
                  <v-text-field
                    v-mask="['(##) ##### - ####']"
                    v-model="form.whatsapp"
                    label="Whatsapp"
                    type="text"
                    prepend-icon="mdi-whatsapp"
                  />
                </v-flex>
              <!--
                <v-flex md12>
                  <v-layout column>
                    <span class="body-2">Logo da Empresa</span>
                    <v-image-input
                      v-model="logo"
                      :image-quality="0.85"
                      :image-height="400"
                      :image-width="600"
                      image-min-scaling="contain"
                      class="arrow-pagintion"
                      clearable
                      image-format="jpeg"
                    />
                  </v-layout>
                </v-flex>
              -->
              </v-layout>
            </v-card-text>
          </v-card>

          <br>

          <v-card class="elevation-0">
            <v-card-title>
              <h3 class="headline mb-0">Dados Bancários para Repasse</h3>
            </v-card-title>
            <v-card-text>
              <!-- Lista de todos os erros do form
                <ul>
                  <li v-for="error in errors.all()" :key="error">{{ error }}</li>
                </ul>
              -->
              <v-layout
                row
                wrap>
                <v-flex md2>
                  <v-autocomplete
                    v-model="form.tpContaBanco"
                    :items="tiposConta"
                    label="Tipo"
                    item-value="id"
                    item-text="descricao"
                  />
                </v-flex>
                <v-flex md4>
                  <v-autocomplete
                    v-model="form.bancoId"
                    :items="bancos"
                    label="Banco"
                    item-value="id"
                    item-text="descricao"
                  />
                </v-flex>
                <v-flex md1>
                  <v-text-field
                    v-model="form.agencia"
                    label="Agência"
                    type="number"
                    reverse
                  />
                </v-flex>
                <v-flex md1>
                  <v-text-field
                    v-model="form.digitoAgencia"
                    label="Dígito Agência"
                    type="number"
                    reverse
                  />
                </v-flex>
                <v-flex md1>
                  <v-text-field
                    v-model="form.conta"
                    label="Conta"
                    type="number"
                    reverse
                  />
                </v-flex>
                <v-flex md1>
                  <v-text-field
                    v-model="form.digitoConta"
                    label="Dígito Conta"
                    type="number"
                    reverse
                  />
                </v-flex>
                <v-flex md2>
                  <v-text-field
                    v-mask="['###.###.###-##', '##.###.###/####-##']"
                    v-model.trim="form.cpfCnpj"
                    label="CPF/CNPJ da Conta"
                    type="text"
                  />
                </v-flex>
                <v-flex md12>
                  <v-textarea
                    v-model="form.observacaoRepasse"
                    label="Observações"
                  />
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>

          <br>

          <v-alert
            :value="true"
            type="warning"
          >
            <span class="subheading">As regras cadastradas na Empresa substitui as regras da Rede, caso não seja cadastrado regras aqui, será utilizado a da Rede.</span>
          </v-alert>

          <br>

          <template v-if="acao === acaoListar">
            <v-card>
              <v-toolbar
                flat
                color="white">
                <v-toolbar-title>Regras da Empresa</v-toolbar-title>
                <v-divider
                  class="mx-2"
                  inset
                  vertical />
                <v-spacer />
                <v-btn
                  small
                  fab
                  color="primary">
                  <v-icon @click="newRegra()">mdi-plus</v-icon>
                </v-btn>
              </v-toolbar>
              <v-progress-linear
                :active="loading"
                color="light-blue"
                indeterminate
                height="3"
              />
              <v-data-table
                :headers="headers"
                :items="regras"
                :total-items="totalItems"
                :pagination.sync="pagination"
                class="elevation-1 arrow-pagintion"
              >
                <template
                  slot="items"
                  slot-scope="{ item }"
                  ma-5
                >
                  <core-tr-item-regra
                    :item="item"
                    :visualizar-regra="visualizarRegra"
                    :alterar-regra="editarRegra"
                    :reload="reload"
                  />
                </template>
              </v-data-table>
            </v-card>
            <v-layout class="justify-end">
              <template>
                <v-btn
                  outline
                  style="color: black !important"
                  @click="cancelar">Cancelar</v-btn>
                <v-btn
                  :loading="loadingBtn"
                  type="submit"
                  color="primary"
                >Salvar</v-btn>
              </template>
            </v-layout>
          </template>
          <template v-else>
            <v-card
              v-if="!disabled"
              class="elevation-0">
              <v-card-text>
                <v-autocomplete
                  v-model="copyId"
                  :items="empresas"
                  item-value="id"
                  item-text="descricao"
                  label="Copiar dados da regra vigente de outra empresa"
                  @change="carregaDadosCopy"
                />
              </v-card-text>
            </v-card>
            <core-regra-financeira
              v-if="!loading"
              v-model="regra"
              :disabled="disabled"
              :salvar="validateBeforeSubmit"
              :voltar="voltar"
              :cancelar="cancelar"
              :acao="acao"
              :primeira-regra="false"
            />
          </template>
        </v-form>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { EmpresaBusiness, TiposBusiness, RegraFinanceiraBusiness } from '../../business'
import RegraFinanceira from '../../beans/RegraFinanceira'
import { ESTADOS, ACAO_NOVA_REGRA, ACAO_VISUALIZAR_REGRA, ACAO_LISTAR_REGRA, ACAO_EDITAR } from '../../constants'
import dateUtils from '../../utils/dateUtils'

export default {
  metaInfo: {
    titleTemplate: '%s - Empresa'
  },
  data () {
    return {
      form: {
        id: null,
        nome: null,
        tpRepasseId: null, // 'INDIFERENTE',
        redeId: null,
        logoId: null,
        tpContaBanco: null,
        bancoId: null,
        agencia: null,
        digitoAgencia: null,
        conta: null,
        digitoConta: null,
        cpfCnpj: null,
        observacaoRepasse: null,
        whatsapp: null,
        nomeProprietario: null,
        email: null
      },
      copyId: null,
      regra: new RegraFinanceira(),
      headers: [
        { class: 'grey lighten-1', sortable: false, text: 'Vigente', align: 'center' },
        { class: 'grey lighten-1', sortable: false, text: 'Vigente Desde', value: 'dtVigencia' },
        { class: 'grey lighten-1', sortable: false, text: 'Multa atraso', align: 'right', value: 'peMultaPorAtraso' },
        { class: 'grey lighten-1', sortable: false, text: 'Juros Mora atraso', align: 'right', value: 'peJurosMoraPorAtraso' },
        { class: 'grey lighten-1', sortable: false, text: 'Tarifa Parcela Banco', align: 'right', value: 'vlTarifaParcelaBanco' },
        { class: 'grey lighten-1', sortable: false, text: 'Tarifa Parcela Loja', align: 'right', value: 'vlTarifaParcelaLoja' },
        { class: 'grey lighten-1', sortable: false, text: 'Cobrar Carnê em Loja', align: 'right', value: 'cobrarCarneLoja' },
        { class: 'grey lighten-1', sortable: false, text: 'Incluída Por', value: 'nomeUsuario' },
        { class: 'grey lighten-1', sortable: false, text: '', value: '', align: 'center' }
      ],
      pagination: {
        rowsPerPage: 10,
        sortBy: 'dataVigencia'
      },
      filtros: {
        empresaId: null
      },
      loading: false,
      logo: null,
      regras: [],
      totalItems: 0,
      valid: false,
      redes: [],
      repasses: [],
      bancos: [],
      tiposConta: [],
      acao: ACAO_LISTAR_REGRA,
      disabled: false,
      acaoListar: ACAO_LISTAR_REGRA,
      loadingBtn: false,
      empresas: []
    }
  },
  watch: {
    pagination: {
      handler () {
        this.paginar()
      }
    }
  },
  mounted () {
    this.reload()
  },
  methods: {
    estados () {
      return ESTADOS.map(estado => estado.sigla)
    },
    validateBeforeSubmit () {
      this.$validator.validate().then(result => {
        if (result) {
          this.loadingBtn = true
          var msgSucesso = !this.form.id ? 'Cadastro realizado com sucesso' : 'Atualização realizada com sucesso'
          EmpresaBusiness.salvar(this.form)
            .then(response => {
              this.form.id = response.data.id
              if ((!!this.regra && !this.regra.id) || this.acao === ACAO_EDITAR) { // só pode salvar a regra se ela for nova, ou seja, id dela é null
                this.regra.empresaId = this.form.id
                RegraFinanceiraBusiness.salvar(this.regra)
                  .then(response => {
                    this.$root.showSucesso(msgSucesso)
                    this.$router.push('/lista-empresa')
                  }).catch(erro => {
                    this.$root.showErro(erro.data)
                    this.flgDeuErro = true
                  })
              } else {
                this.$root.showSucesso(msgSucesso)
                this.$router.push('/lista-empresa')
              }
            }).catch(error => {
              this.$root.showErro(error.data)
              this.flgDeuErro = true
            }).finally(() => {
              this.loadingBtn = false
            })
            /*
              .then(response => {
              if (this.logo) {
                let formData = new FormData()
                formData.append('file', this.logo)
                formData.append('nome', 'empresa-logo')

                EmpresaBusiness.uploadLogo(this.form.id, formData)
                  .catch(error => this.$root.showErro(error.data))
              }
              return response
            })
            */
        } else {
          this.$root.showAlerta(this.$vuetify.t('$vuetify.Erro.camposObrigatorios'))
        }
      })
    },
    cancelar () {
      this.$router.push('/lista-empresa')
    },
    voltar () {
      this.reset()
    },
    visualizarRegra (regra) {
      this.disabled = true
      RegraFinanceiraBusiness.getById(regra.id)
        .then(response => {
          this.regra = response.data
          this.regra.dataVigencia = this.$moment(response.data.dataVigencia).format('YYYY-MM-DD')
          this.regra.dataVigenciaLancamento = this.$moment(response.data.dataVigenciaLancamento).format('YYYY-MM-DD')
          this.acao = ACAO_VISUALIZAR_REGRA
        })
        .catch(() => this.$root.showAlerta('Problemas para recuperar os dados da regra, tentar mais tarde'))
    },
    editarRegra (regra) {
      RegraFinanceiraBusiness.getById(regra.id)
        .then(response => {
          this.regra = response.data
          this.regra.dataVigencia = this.$moment(response.data.dataVigencia).format('YYYY-MM-DD')
          if (response.data.dataVigenciaLancamento) {
            this.regra.dataVigenciaLancamento = this.$moment(response.data.dataVigenciaLancamento).format('YYYY-MM-DD')
          }
          this.acao = ACAO_EDITAR
        })
        .catch(() => this.$root.showAlerta('Problemas para recuperar os dados da regra, tentar mais tarde'))
    },
    carregaDadosCopy (empresaId) {
      this.loading = true
      EmpresaBusiness.getRegrafinanceiraVigenteById(empresaId)
        .then(response => {
          this.regra = { ...response.data }
          this.regra.dataVigencia = dateUtils.addDia(new Date(), 1)
          this.regra.dataVigenciaLancamento = null
          this.regra.id = null
          this.regra.isVigente = false
        })
        .catch(erro => {
          this.$root.showAlerta('Problemas para copiar dados: ' + erro)
        })
        .finally(() => {
          this.loading = false
        })
    },
    newRegra () {
      this.acao = ACAO_NOVA_REGRA
      this.regra = new RegraFinanceira()
      TiposBusiness.getAllEmpresas().then(response => {
        this.empresas = response.data
      })
    },
    paginar () {
      if (!this.filtros.empresaId) return
      this.loading = true
      RegraFinanceiraBusiness.pagination(this.pagination.rowsPerPage, this.pagination.page - 1, this.pagination.sortBy || 'dataVigencia', this.filtros)
        .then(response => {
          this.regras = response.data.content
          this.totalItems = response.data.totalElements
        })
        .catch(() => this.$root.showErro('Erro ao carregar as regras da rede'))
        .finally(() => {
          this.loading = false
        })
    },
    reset () {
      this.acao = ACAO_LISTAR_REGRA
      this.disabled = false
      this.regra = null
    },
    reload () {
      this.reset()
      let id = this.$route.params.id
      if (id) {
        EmpresaBusiness.getById(id)
          .then(response => {
            this.form = response.data
            this.filtros.empresaId = id
            /* retirei loja da empresa
            if (this.form.logoId) {
              AnexoBusiness.getById(this.form.logoId).then(response => {
                this.logo = response.data.strBase64
              })
            }
            */
            this.paginar()
          })
      }
      TiposBusiness.getAllRedes().then(response => { this.redes = response.data })
      TiposBusiness.listRepasse().then(response => { this.repasses = response.data })
      TiposBusiness.listTiposContaBanco().then(response => { this.tiposConta = response.data })
      TiposBusiness.listBancos().then(response => { this.bancos = response.data })
    }
  }
}
</script>
